import React from 'react';

// Basic test to verify the component can be imported
describe('SettingsPage - Import Test', () => {
  test('can import SettingsPage component without hanging', async () => {
    // This test just verifies that the component can be imported
    // without causing Jest to hang
    const SettingsPageModule = await import('../page');
    expect(SettingsPageModule.default).toBeDefined();
    expect(typeof SettingsPageModule.default).toBe('function');
  });
}); 